/**
 * @fileoverview Layout component
 */
import React, { FC, useEffect, useState } from "react";
import { ThunkDispatch } from "redux-thunk";
import { AnyAction } from "redux";
import { connect } from "react-redux";
import {
  CountryInfo,
  fetchCountries,
  CountryData,
  fetchData2,
  fetchGdpGrowthActions,
  fetchPopGrowthActions,
  fetchGdpActions,
  fetchPricingActions,
  fetchInflationActions,
  fetchExpanseActions,
  fetchPriceDeflatorActions
} from "../store/actions";
import { RootState } from "../store";
import BarsButton from "./UI/BarsButton";
import Spinner from "./UI/Spinner";
import SideBar from "./UI/SideBar";
import BackupTimeseriesLine from "./Charts/BackupTimeseriesLine";
import BackupPie from "./Charts/BackupPie";
import BackupBars from "./Charts/BackupBars";
import BackupMultiLines from "./Charts/BackupMultiLines";
import TimeseriesLine from "./Charts/TimeseriesLine";
import Pie from "./Charts/Pie";
import Bars from "./Charts/Bars";
import MultiLines from "./Charts/MultiLines";
import Styles from "./Layout.module.css";

type Props = {
  loadingCountries: boolean,
  countries: CountryInfo[],
  getCountries: () => void,
  countryGdpData: CountryData[],
  countryPopGrowthData: CountryData[],
  countryGdpGrowthData: CountryData[],
  countryPricingData: CountryData[],
  countryExpanseData: CountryData[],
  countryInflationData: CountryData[],
  countryPriceDeflatorData: CountryData[],
  getCountryGdpData: (country: string, indicator: string) => void,
  getCountryGdpGrowthData: (country: string, indicator: string) => void,
  getCountryPopGrowthData: (country: string, indicator: string) => void,
  getCountryPricingData: (country: string, indicator: string) => void,
  getCountryInflationData: (country: string, indicator: string) => void,
  getCountryExpanseData: (country: string, indicator: string) => void,
  getCountryPriceDeflatorData: (country: string, indicator: string) => void,
};
const Layout: FC<Props> = (props) => {

  const [sideBarOpen, setSideBarOpen] = useState<boolean>(false);

  useEffect(() => {
    if (props.countries && props.countries.length === 0) {
      props.getCountries();
    }
  }, [props]);

  const barsOnClickHandler = () => {
    setSideBarOpen(true);
  }

  const listItemOnClickHandler = (id: string) => {
    setSideBarOpen(false);
    // NY.GDP.MKTP.CD    - GDP (current US$)
    // NY.GDP.MKTP.KD.ZG - GDP growth (annual %)
    // FM.LBL.BMNY.GD.ZS - Broad money (% of GDP)
    // FP.CPI.TOTL       - Consumer price index (2010 = 100)
    // FP.CPI.TOTL.ZG    - Inflation, consumer prices (annual %)
    // NY.GDP.DEFL.KD.ZG - Inflation, GDP deflator (annual %)
    // SP.POP.GROW       - Population growth (annual %)
    // GC.XPN.TOTL.GD.ZS - Expense (% of GDP)
    props.getCountryGdpData(id, "NY.GDP.MKTP.CD");
    props.getCountryGdpGrowthData(id, "NY.GDP.MKTP.KD.ZG");
    props.getCountryPopGrowthData(id, "SP.POP.GROW");
    props.getCountryExpanseData(id, "GC.XPN.TOTL.GD.ZS");
    props.getCountryPricingData(id, "FP.CPI.TOTL");
    props.getCountryInflationData(id, "FP.CPI.TOTL.ZG");
    props.getCountryPriceDeflatorData(id, "NY.GDP.DEFL.KD.ZG");
  }

  const clickedHandler = () => {
    setSideBarOpen(false);
  }

  const createPieChart = (chartData: CountryData[]) => {
    if (chartData[0] && chartData.length > 0) {
      const year = chartData.find(d => d.value !== null);
      if (year) {
        return (
          <Pie
            title={`${year.date} ${year.country.value} ${year.indicator.value}`}
            width={360}
            height={300} data={[{ key: new Date(+year.date, 11, 31), value: year.value },
            { key: new Date(+year.date, 11, 31), value: 100 - year.value }]} />
        );
      }
    }
    return (<BackupPie />);
  }

  const createLineChart = (chartData: CountryData[]) => {
    let [info] = chartData;
    if (info && chartData.length > 0) {
      return (
        <TimeseriesLine
          width={360}
          height={300}
          margin={{ top: 20, right: 50, bottom: 50, left: 50 }}
          title={`${info.country.value} ${info.indicator.value}`}
          data={chartData.map(d => ({ key: new Date(+d.date, 11, 31), value: d.value }))}
          showXaxis={true}
          showYaxis={true}
          timeYearInterval={Math.min(10, chartData.length)} />
      );
    }
    return (<BackupTimeseriesLine />);
  };

  const createbarChart = (chartData: CountryData[]) => {
    let [info] = chartData;
    if (info && chartData.length > 0) {
      return (
        <Bars
          width={360}
          height={300}
          margin={{ top: 20, right: 50, bottom: 50, left: 50 }}
          title={`${info.country.value} ${info.indicator.value}`}
          data={chartData.map(d => ({ key: new Date(+d.date, 11, 31), value: d.value }))}
          showXaxis={true}
          showYaxis={true}
          timeYearInterval={Math.min(10, chartData.length)} />
      );
    }
    return (<BackupBars />);
  };

  const createMultLinesChart = (chartDataT: CountryData[], chartDataC: CountryData[]) => {
    let [t] = chartDataT;
    let [c] = chartDataC;
    if (t && c) {
      let chartData = chartDataT.concat(...chartDataC);
      return (
        <MultiLines
          width={360}
          height={300}
          margin={{ top: 10, right: 50, bottom: 50, left: 50 }}
          titles={[{ indicator: t.indicator.id, title: `${t.country.value} ${t.indicator.value}` },
          { indicator: c.indicator.id, title: `${c.country.value} ${c.indicator.value}` }]}
          data={chartData.map(d =>
            ({ indicator: d.indicator.id, key: new Date(+d.date, 11, 31), value: d.value }))}
          showXaxis={true}
          showYaxis={true}
          timeYearInterval={Math.min(10, chartData.length)} />
      );
    }
    return (<BackupMultiLines />);
  };

  return (
    <div>
      <BarsButton clicked={barsOnClickHandler} />
      <div className={Styles.row}>
        <div className={[Styles.col2, sideBarOpen ? Styles.col2Open : ''].join(' ')}>
          {props.loadingCountries ? <Spinner /> :
            <SideBar listItems={props.countries.map(
              d => ({ name: d.name, id: d.iso2Code })
            )} clicked={listItemOnClickHandler} />}
        </div>
        <div className={Styles.col3} onClick={clickedHandler}>
          {createLineChart(props.countryGdpData)}
        </div>
        <div className={Styles.col3} onClick={clickedHandler}>
          {createLineChart(props.countryGdpGrowthData)}
        </div>
        <div className={Styles.col3} onClick={clickedHandler}>
          {createPieChart(props.countryExpanseData)}
        </div>
        <div className={Styles.col3} onClick={clickedHandler}>
          {createbarChart(props.countryPopGrowthData)}
        </div>
        <div className={Styles.col3} onClick={clickedHandler}>
          {createLineChart(props.countryPricingData)}
        </div>
        <div className={Styles.col3} onClick={clickedHandler}>
          {createMultLinesChart(props.countryInflationData,
            props.countryPriceDeflatorData)}
        </div>
      </div>
    </div>
  )
};

const mapStateToProps = (state: RootState) => {
  return {
    loadingCountries: state.fetchCountriesState.loading,
    countries: state.fetchCountriesState.countries,
    countryGdpData: state.fetchDataState.countryGdpData,
    countryGdpGrowthData: state.fetchDataState.countryGdpGrowthData,
    countryPopGrowthData: state.fetchDataState.countryPopGrowthData,
    countryPricingData: state.fetchDataState.countryPricingData,
    countryInflationData: state.fetchDataState.countryInflationData,
    countryExpanseData: state.fetchDataState.countryExpanseData,
    countryPriceDeflatorData: state.fetchDataState.countryPriceDeflatorData,
  }
};

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AnyAction>) => {
  return {
    getCountries: () => dispatch(fetchCountries()),
    getCountryGdpGrowthData: (country: string, indicator: string) =>
      dispatch(fetchData2(country, indicator, fetchGdpGrowthActions)),
    getCountryPopGrowthData: (country: string, indicator: string) =>
      dispatch(fetchData2(country, indicator, fetchPopGrowthActions)),
    getCountryGdpData: (country: string, indicator: string) =>
      dispatch(fetchData2(country, indicator, fetchGdpActions)),
    getCountryPricingData: (country: string, indicator: string) =>
      dispatch(fetchData2(country, indicator, fetchPricingActions)),
    getCountryInflationData: (country: string, indicator: string) =>
      dispatch(fetchData2(country, indicator, fetchInflationActions)),
    getCountryExpanseData: (country: string, indicator: string) =>
      dispatch(fetchData2(country, indicator, fetchExpanseActions)),
    getCountryPriceDeflatorData: (country: string, indicator: string) =>
      dispatch(fetchData2(country, indicator, fetchPriceDeflatorActions)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Layout);
