/**
 * @fileoverview Backup (Fake) pie chart component 
 */
import React, { FC } from "react";
import Pie from "./Pie";

const BackupPie: FC = () => {

  const todayYear = (new Date()).getFullYear();

  return (
    <Pie
      title={`${todayYear} annual growth %`}
      width={360}
      height={300} data={[{ key: new Date(2019, 11, 31), value: 32.85699280397323 },
      { key: new Date(2018, 11, 31), value: 67.14300719602677 }]}
      indeterminate={true} />
  );
};

export default BackupPie;
