/**
 * @fileoverview Bullet point component
 */
import React, { FC } from "react";
import Styles from './BulletPoint.module.css';

type Props = { title: string, color: string }
const BulletPoint: FC<Props> = (props) => {
  return (
    <div>
      <span className={Styles.bulletPoint}
        style={{ background: props.color }}></span>
      <span className={Styles.title}>{props.title}</span>
    </div>
  );
};

export default BulletPoint;
