/**
 * @fileoverview Backup (Fake) bars chart component 
 */
import React, { FC } from "react";
import Bars from "./Bars";

const BackupBars: FC = () => {

  const init = [];
  for (let i = 1980; i < 2020; i++) {
    init.push({
      key: new Date(i, 11, 31),
      value: (Math.random() * 10) - 5
    });
  }

  return (
    <Bars width={360}
      height={300}
      margin={{ top: 20, right: 50, bottom: 50, left: 50 }}
      title={'Chart of Growth'}
      data={init}
      showXaxis={true}
      showYaxis={true}
      timeYearInterval={5}
      indeterminate={true} />
  );
};

export default BackupBars;
