/**
 * @fileoverview Backup (fake) multi line
 */
import React from "react";
import MultiLines from "./MultiLines";

const BackupMultiLines = () => {

  const init = [];
  for (let i = 1980; i < 2020; i++) {
    init.push({
      indicator: 'Today',
      key: new Date(i, 11, 31),
      value: (Math.random() * 10) - 5,
    });
  }
  for (let i = 1980; i < 2020; i++) {
    init.push({
      indicator: 'After',
      key: new Date(i, 11, 31),
      value: (Math.random() * 10) - 5,
    });
  }

  return (
    <MultiLines
      width={360}
      height={300}
      margin={{ top: 20, right: 50, bottom: 50, left: 50 }}
      titles={[{ indicator: 'Today', title: 'Today Growth' },
      { indicator: 'After', title: 'After Growth' }]}
      data={init}
      showXaxis={true}
      showYaxis={true}
      timeYearInterval={5} />
  );
};

export default BackupMultiLines;
