/**
 * @fileoverview Fetch data reducer
 */
import { CountryData } from "../actions";
import { Actions, Types } from "../actions/actionTypes";

export interface FetchDataState {
  countryGdpGrowthData: CountryData[],
  countryPopGrowthData: CountryData[],
  countryGdpData: CountryData[],
  countryPricingData: CountryData[],
  countryInflationData: CountryData[],
  countryPriceDeflatorData: CountryData[],
  countryExpanseData: CountryData[],
  loading: boolean,
  error: string | null,
}

const initialState: FetchDataState = {
  countryGdpGrowthData: [],
  countryPopGrowthData: [],
  countryGdpData: [],
  countryPricingData: [],
  countryInflationData: [],
  countryExpanseData: [],
  countryPriceDeflatorData: [],
  loading: false,
  error: null,
};

const reducer = (state = initialState, action: Actions): FetchDataState => {
  switch (action.type) {
    case Types.FETCH_GDP_DATA_START:
      return {
        ...state,
        countryGdpData: [],
        loading: true,
        error: null,
      }
    case Types.FETCH_GDP_DATA_SUCCEEDED:
      return {
        ...state,
        countryGdpData: state.countryGdpData.concat(action.payload),
        loading: false,
        error: null,
      };
    case Types.FETCH_GDP_DATA_FAILED:
      return {
        ...state,
        countryGdpData: [],
        loading: false,
        error: action.payload,
      };
    case Types.FETCH_POP_GROWTH_DATA_START:
      return {
        ...state,
        countryPopGrowthData: []
      };
    case Types.FETCH_POP_GROWTH_DATA_SUCCEEDED:
      return {
        ...state,
        countryPopGrowthData: state.countryPopGrowthData.concat(action.payload),
      };
    case Types.FETCH_POP_GROWTH_DATA_FAILED:
      return {
        ...state,
        countryPopGrowthData: [],
      };
    case Types.FETCH_PRICING_DATA_START:
      return {
        ...state,
        countryPricingData: [],
      };
    case Types.FETCH_PRICING_DATA_SUCCEEDED:
      return {
        ...state,
        countryPricingData: state.countryPricingData.concat(action.payload),
      };
    case Types.FETCH_PRICING_DATA_FAILED:
      return {
        ...state,
        countryPricingData: [],
      };
    case Types.FETCH_INFLATION_DATA_START:
      return {
        ...state,
        countryInflationData: [],
      };
    case Types.FETCH_INFLATION_DATA_SUCCEEDED:
      return {
        ...state,
        countryInflationData: state.countryInflationData.concat(action.payload),
      };
    case Types.FETCH_INFLATION_DATA_FAILED:
      return {
        ...state,
        countryInflationData: [],
      };
    case Types.FETCH_GDP_GROWTH_DATA_START:
      return {
        ...state,
        countryGdpGrowthData: [],
      };
    case Types.FETCH_GDP_GROWTH_DATA_SUCCEEDED:
      return {
        ...state,
        countryGdpGrowthData: state.countryGdpGrowthData.concat(action.payload),
      };
    case Types.FETCH_GDP_GROWTH_DATA_FAILED:
      return {
        ...state,
        countryGdpGrowthData: [],
      };
    case Types.FETCH_EXPANSE_DATA_START:
      return {
        ...state,
        countryExpanseData: [],
      };
    case Types.FETCH_EXPANSE_DATA_SUCCEEDED:
      return {
        ...state,
        countryExpanseData: state.countryExpanseData.concat(action.payload),
      };
    case Types.FETCH_EXPANSE_DATA_FAILED:
      return {
        ...state,
        countryExpanseData: [],
      };
    case Types.FETCH_PRICE_DEFLATOR_DATA_START:
      return {
        ...state,
        countryPriceDeflatorData: [],
      };
    case Types.FETCH_PRICE_DEFLATOR_DATA_SUCCEEDED:
      return {
        ...state,
        countryPriceDeflatorData: state.countryPriceDeflatorData.concat(action.payload),
      };
    case Types.FETCH_PRICE_DEFLATOR_DATA_FAILED:
      return {
        ...state,
        countryPriceDeflatorData: [],
      };
    default:
      return state;
  }
};

export default reducer;
